<template>
    <div class="m-category-list">
        <span class="u-category"></span>
        <el-radio-group v-model="category" @input="updateCategory">
            <el-tooltip v-for="item in options" :key="item.name" effect="dark" :content="item.remark" placement="top-start">
                <el-radio border :label="item.name">{{ item.name }}</el-radio>
            </el-tooltip>
        </el-radio-group>
    </div>
</template>

<script>
export default {
    props: {
        options: {
            type: Array,
            required: true,
        },
        value: {
            type: [String, Number],
            default: "",
        },
    },
    data() {
        return {
            category: this.value,
        };
    },
    watch: {
        //  value 
        value(newVal) {
            this.category = newVal;
        },
        options: {
            deep: true,
            immediate: true,
            handler() {
                if (!this.value) {
                    this.category = this.options?.[0]?.name;
                    this.$emit("update", this.category);
                }
            }
        }
    },
    model: {
        prop: "value",
        event: "update",
    },
    methods: {
        //  radio-group  category  value 
        updateCategory(newVal) {
            this.$emit("update", newVal);
        },
    },
};
</script>

